/* Basic styling for the footer container */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 400px;
  height: 50px;
  background-color: black;
  color: #fff;
  padding: 5px 10px;
  padding-bottom: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  border-top: 2px solid white;
  border-right: 2px solid white;
  /* overflow: hidden;
  z-index: 1000; */
}

/* Style for the play/pause button */
.footer button {
  padding: 1px 5px;
  background-color: inherit;
  color: white;
  border: none;
  cursor: pointer;
  /* font-size: 2em; */
}

.volume-button {
  font-size: 1.6em;
}

.playpause-button {
  font-size: 2.2em;
  padding: 0;
  margin: 0;
}

.spin-icon {
  animation: spin 1s linear infinite;
  font-size: 0.7em;
  padding: 10px 10px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes marquee {
  0% {
    transform: translateX(70%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.marquee-wrapper {
  flex-grow: 0;
  width: 250px; /* Adjust to the width you want */

  overflow: hidden; /* Prevent the marquee from going outside the wrapper */
  position: relative;
}

.marquee-wrapper::before,
.marquee-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10px; /* adjust width to control the size of the blur */
  pointer-events: none;
  z-index: 2;
}

.marquee-wrapper::before {
  left: 0;
  background: linear-gradient(to right, black, transparent); /* Change color as needed */
}

.marquee-wrapper::after {
  right: 0;
  background: linear-gradient(to left, black, transparent); /* Change color as needed */
}

.marquee {
  white-space: nowrap;

  display: inline-block;
  animation: marquee 15s linear infinite;
  z-index: 1;
}

@media (max-width: 769px) {
  .marquee-wrapper {
    width: 260px; /* Adjust to the width you want */
  }
  .footer {
    height: 6vh;
    width: 100%;
    padding: 15px 5px 10px 10px;
    border-top: 1px solid white;
    border-right: none;
    margin: 0;
  }
  /* 
  .playpause-button {
  margin-top: 3px;
  } */
}

@media (max-width: 380px) {
  .marquee-wrapper {
    width: 160px;
  }

  .footer {
    height: 40px;
    width: 100%;
    padding-top: 7px;
    padding-bottom: 0;
    border-top: 1px solid white;
    border-right: none;
  }

  .spin-icon {
    animation: spin 1s linear infinite;
    font-size: 0.6em;
    padding: 5px 5px;
  }
}
